import { useState, useEffect, createContext } from 'react';
import PadContainer from './components/PadContainer';
import Controls from './components/Controls';
import $ from 'jquery';
// DATA
import { heaterKit, smoothPianoKit } from './data/Variants';



export const ControlContext = createContext();

function App() {
  const [ currentBank, changeCurrentBank] = useState(heaterKit); // default bank will be heaterkit.
  const [ bankDefaultStatus, changeBankStatus ] = useState(true); // status default is true
  const [ volume, setVolume ] = useState(.5); // volume
  const [ lastClip, setLastClip ] = useState(''); 
  const [ isTurnedOn, togglePowerSwitch ] = useState(false); // power switch 
  
  

  function toggleBank() {
    changeBankStatus(!bankDefaultStatus);
    const bank = (!bankDefaultStatus) ? "Heater Kit" : "Smooth Piano Kit";
    setLastClip(isTurnedOn?bank:'')
  }
  useEffect(() => {
    const changeInto = bankDefaultStatus?heaterKit:smoothPianoKit;
    changeCurrentBank(changeInto);
  }, [bankDefaultStatus])

  useEffect(() => {
      const bank = (bankDefaultStatus) ? "Heater Kit" : "Smooth Piano Kit";
      setLastClip(isTurnedOn?bank:'')
  }, [isTurnedOn, bankDefaultStatus])

  useEffect(() => {
    const keysAvailable = currentBank.map(item=>item.keyTrigger);
    $(window).on("keydown", (e)=>{
      const keyboardPress = e.key.toUpperCase();
      if(keysAvailable.includes(keyboardPress)){
        const clipId = currentBank.filter(n=>n.keyTrigger===keyboardPress)[0].id.replace(/'/g,"");
        $(`#${clipId}`).trigger("click");
      }
    })
    return ()=> {
      $(window).off("keydown");
    }
  },[currentBank]);
  
  return (
    <div 
    className="w-full h-screen bg-green-200 p-8 flex flex-col justify-center items-center" 
    id="drum-machine">

      <div className="w-full h-full flex flex-col flex-1 justify-center items-center gap-4 lg:flex-row">
        <ControlContext.Provider value={
            {
              volume,
              isTurnedOn,
              setLastClip
            }}>
          <PadContainer 
          bank={currentBank}
          />
        </ControlContext.Provider>

        <Controls 
        clip={lastClip} 
        toggleBank={toggleBank} 
        togglePowerSwitch={togglePowerSwitch} 
        isTurnedOn={isTurnedOn}
        bankDefaultStatus={bankDefaultStatus}
        setVolume={setVolume}
        volume={volume}/>
      </div> 

      {/* Simple footer */}
      <p className="text-green-900">Made by <a className="underline" href="https://github.com/druloloy">@druloloy</a> with &#9829;</p>
    </div>
    
  )
}

export default App
