import { useContext } from 'react'
import $ from 'jquery';

import { ControlContext } from '../App';


function Button( { bank } ) {
    const Control = useContext(ControlContext);
    const { volume, isTurnedOn, setLastClip } = Control;
    async function buttonClick(e){

        if(isTurnedOn){
            setLastClip(bank.id);
            $(e.target).addClass("bg-blue-500 shadow-inner")
            const audio = e.target.lastChild;
            audio.volume = volume;
            audio.currentTime = 0;  
            try {
                await audio.play();
            } catch (error) {
                console.log(error);
            }
            finally {
                setTimeout(() => {
                        $(e.target).removeClass("bg-blue-500 shadow-inner")
                    }, 150);  
            }
        }

    }
    function activeButton(e){
        if(!isTurnedOn){
            $(e.target).removeClass("cursor-pointer");
        }else{
            $(e.target).addClass("cursor-pointer");
        }
    }
    return (
        <div
        className="drum-pad bg-blue-100 font-bold flex justify-center items-center rounded-xl select-none outline-none shadow-md transition-colors cursor-pointer"
        id={bank.id.replace(/'/g,'')}   
        onClick={buttonClick}
        onMouseOver={activeButton}>  
            <span className="select-none pointer-events-none text-green-900 text-xl">{bank.keyTrigger}</span>
            
            <audio id={bank.keyTrigger} className="clip" src={bank.url}></audio>
        </div>
    )
}

export default Button
