import React from 'react'
import Switch from 'react-switch';
function Controls( { clip, togglePowerSwitch, bankDefaultStatus, toggleBank, setVolume, volume, isTurnedOn } ) {
    return (
        <div className="h-auto justify-center items-center gap-4 p-6 xs:flex-col" >

            <div className="w-full h-16 bg-blue-100 rounded-2xl flex justify-center items-center">
                <p className="text-center text-lg text-green-900" id="display">{ clip.replace(/-/g,' ') }</p>
            </div>

            <div className="flex flex-row w-full h-auto justify-evenly items-center mt-4 text-green-900 gap-8" id="switches">
                <div className="flex justify-center gap-2">
                    <span id="power-label">Power</span>
                    <Switch onChange={()=>togglePowerSwitch(!isTurnedOn)} 
                    checked={isTurnedOn}
                    onColor="#DBEAFE"
                    onHandleColor="#3B82F6"
                    offColor="#064E3B"
                    handleDiameter={30}
                    uncheckedIcon={false}
                     checkedIcon={false}
                    boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
                    activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
                    height={20}
                    width={48}
                    className="switch"
                    aria-labelledby="power-label"
                    id="bank-switch" />
                </div>
                <div className="flex justify-center item gap-2">
                    <span id="bank-label">Bank</span>
                    <Switch onChange={toggleBank} 
                    checked={bankDefaultStatus}
                    onColor="#DBEAFE"
                    onHandleColor="#3B82F6"
                    offColor="#064E3B"
                    handleDiameter={30}
                    uncheckedIcon={false}
                    checkedIcon={false}
                    boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
                    activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
                    height={20}
                    width={48}
                    className="switch"
                    aria-labelledby="bank-label"
                    id="bank-switch" />
                </div>
            </div>
            <div id="volume" className="w-full mt-4 flex flex-row justify-center items-center gap-2 flex-1">
                {
                    (volume>0&&isTurnedOn)? 
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-green-900" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15.536 8.464a5 5 0 010 7.072m2.828-9.9a9 9 0 010 12.728M5.586 15H4a1 1 0 01-1-1v-4a1 1 0 011-1h1.586l4.707-4.707C10.923 3.663 12 4.109 12 5v14c0 .891-1.077 1.337-1.707.707L5.586 15z" /></svg>
                    :
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-green-900" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5.586 15H4a1 1 0 01-1-1v-4a1 1 0 011-1h1.586l4.707-4.707C10.923 3.663 12 4.109 12 5v14c0 .891-1.077 1.337-1.707.707L5.586 15z" clipRule="evenodd" />
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2" /></svg>
                }
                <input 
                className="w-full"
                id="volume-racker" 
                type="range" 
                step={.25}
                min="0"
                max="1"
                onChange={(e)=>setVolume(e.target.valueAsNumber)}
                value={volume} />
                <span className="text-green-900 w-14 select-none">{volume*100}%</span>
            </div>
        </div>
    )
}

export default Controls
