import React from 'react'

import Button from './Button';

function PadContainer({ bank, volume, setClip, isTurnedOn }) {
    return (
        <div className="w-full max-w-sm h-2/3 grid grid-cols-3 gap-2" id="drum-pads-holder">
            {
                bank.map(item => (<Button key={item.id} bank={item}/>))
            }
        </div>
    )
}

export default PadContainer
